@charset "UTF-8";
* {
  font-family: Montserrat, sans-serif; }

.title-container {
  width: 248px; }

.title-container-4 {
  margin: 100px 0 80px;
  width: 351px; }

.title-number {
  font-size: 14px;
  line-height: 40px;
  letter-spacing: -0.14px; }

.title-first-line {
  display: flex;
  align-items: flex-start; }

.justify-end {
  justify-content: flex-end; }

.sub-title {
  font-size: 13px;
  line-height: 23px;
  letter-spacing: 0.42px; }
  .sub-title-star {
    letter-spacing: 0.42px; }
  .sub-title-w40 {
    width: 40%; }

.text {
  font-size: 55px;
  letter-spacing: -0.74px; }

.uppercase {
  text-transform: uppercase; }

.fw-light {
  font-weight: 300; }
  .fw-lighten {
    font-weight: 200; }
  .fw-light-bold {
    font-weight: 500; }

.fw-normal {
  font-weight: 400; }

.fw-bold {
  font-weight: 600; }

.fw-bolder {
  font-weight: 800; }

.color-dark {
  color: #121120; }

.color-blue {
  color: #0043ff; }

.mt-200 {
  margin-top: 200px; }

.mt-150 {
  margin-top: 150px; }

.mt-100 {
  margin-top: 100px; }

.mt-75 {
  margin-top: 75px; }

.mt-50 {
  margin-top: 50px; }

.mt-30 {
  margin-top: 30px; }

.mt-20 {
  margin-top: 20px; }

.mb-55 {
  margin-bottom: 55px; }

.mb-20 {
  margin-bottom: 20px; }

.animation {
  z-index: 1;
  overflow-x: hidden;
  height: 64px;
  width: 100%;
  background-position: 1% 50%;
  background-image: url("./ppp.png");
  animation: move 50s infinite linear; }

@keyframes move {
  from {
    background-position: 1000% 50%; }
  to {
    background-position: 1% 50%; } }

.title-4-bgi {
  width: 255px;
  height: 43px;
  background-image: url("./просто.png");
  background-size: cover;
  position: absolute;
  left: 165px; }
