.footer-content {
  display: flex;
  align-items: center;
  justify-content: space-between; }

.footer-title {
  width: 60%; }

.footer-form {
  width: 40%; }

.footer-footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 50px; }

.link {
  color: #121120;
  font-size: 13px;
  font-weight: 300;
  letter-spacing: 0.35px; }
  .link a {
    text-decoration: none;
    color: #121120; }
    .link a:hover {
      color: #0043ff; }
  .links {
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: space-between; }
