.bonus-section {
  margin-top: 190px; }
  .bonus-section-text {
    color: #121120;
    font-size: 12px;
    line-height: 17px;
    letter-spacing: 0.42px; }
    .bonus-section-text-wrapper {
      margin-right: 50px;
      width: calc(25% - 50px); }
  .bonus-section-flex {
    display: flex;
    justify-content: space-between; }
  .bonus-section-delimiter {
    height: 1px;
    background-color: #121120;
    opacity: 0.6; }
  .bonus-section-title {
    color: #0043ff;
    font-size: 26px;
    font-weight: 400;
    text-transform: uppercase; }
    .bonus-section-title-block {
      width: 25%;
      align-content: center; }
    .bonus-section-title-light {
      color: #171725;
      font-weight: 700;
      letter-spacing: -0.35px; }
    .bonus-section-title-bold {
      font-weight: 900;
      letter-spacing: -0.35px; }
