.round-animation {
  position: relative;
  display: flex;
  justify-content: center; }
  .round-animation-block {
    z-index: 5;
    position: absolute;
    top: 220px;
    left: 355px;
    width: 260px;
    height: 260px;
    transform-origin: center center;
    animation: rotation 5s 1 linear; }
  .round-animation span {
    height: 260px;
    position: absolute;
    width: 8px;
    color: #ffffff;
    font-family: Montserrat, MonoSpace, monospace;
    font-size: 10px;
    font-weight: 300; }

@keyframes rotation {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(359deg); } }

