.form {
  width: 60%; }

.input {
  margin-top: 15px;
  background-color: transparent;
  border: none;
  width: calc(100% - 40px);
  height: 30px;
  border-bottom: 1px solid #666774;
  outline: none;
  opacity: 0.6;
  color: #121120;
  font-weight: 700;
  padding-right: 40px; }
  .input-valid {
    width: 19px;
    height: 14px;
    right: 10px;
    top: 22px;
    position: absolute;
    background-image: url("./valid.png"); }
    .input-valid-textarea {
      top: 36px; }
  .input-container {
    position: relative; }

.textarea {
  height: 40px;
  line-height: 20px;
  overflow: auto; }

.error-message {
  color: red;
  font-size: 11px;
  font-weight: 600;
  margin-top: -15px; }

.submit {
  padding: 0; }
  .submit-container {
